import React, { useCallback } from 'react'
import Isvg from 'react-inlinesvg'

export const ButtonIcon = ({ text, icon, active, handleShowActiveMenu }) => {

    const handleActive = useCallback(() => {
        handleShowActiveMenu && handleShowActiveMenu(text);

    }, [text])

    return (
        <button onClick={handleActive} className={`button-icon ${active ? "active" : ""}`}> {icon && <Isvg src={icon} />} {text} </button>
    )
}