import React from 'react';
import { Link } from 'react-router-dom';
import { StarRating } from '../StarRating';

export const TopAttractions = ({ img, title, desc, value, rating, slug }) => {

    return (
        <Link to={`/activities-link/${slug}`} style={{ textDecoration: "none" }}>
            <div className="top-attractions-wrapper-card">
                <div className="top-attractions-card">
                    <span>{value}</span>
                    <img src={img} alt="top attr" />
                    <div className="content-text">
                        <h2>{title}</h2>
                        <div>
                            {
                                [0, 1, 2, 3, 4].map((starIndex) => <StarRating key={starIndex} value={(rating - starIndex) * 100} color="#FFCB45" />)
                            }
                        </div>
                        <p>{desc}</p>
                    </div>
                </div>
            </div>
        </Link>
    )
}