import axios from 'axios';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { ElfsightWidget } from 'react-elfsight-widget';
import Isvg from 'react-inlinesvg';
import { createSearchParams, Link, useSearchParams } from 'react-router-dom';
import { ButtonIcon } from '../components/ButtonIcon';
import { ThirdGuideCard } from '../components/Guide/GuideCard/ThirdGuideCard';
import { Slider } from '../components/Guide/Slider';
import { PropertyOffers } from '../components/PropertyOffers';
import { Subscribe } from '../components/Subscrube';
import { Title } from '../components/Title';
import { TopAttractions } from '../components/TopAttractionsCard';
import { HOST_NAME, TOP_6_ATTRACTIONS } from '../config';
import { DefaultLayout } from '../containers/DefaultLayout';
import ActivitiesLandingPageCover from '../images/ActivitiesLandingPage.png';
import ArrowDown2 from '../images/svg/arrow-down2.svg';
import BxsBinoculars from '../images/svg/bxs_binoculars.svg';
import EmojioneMonotone from '../images/svg/emojioneMonotone.svg';
import EntypoTicket from '../images/svg/entypo_ticket.svg';
import BowlFood from '../images/svg/fa6-solid_bowl-food.svg';
import HealthiconsTraining from '../images/svg/healthicons_i-training-class.svg';
import BaselineOutdoor from '../images/svg/ic_baseline-outdoor-grill.svg';
import MapArtGallery from '../images/svg/map_art-gallery.svg';
import SymbolsTransportation from '../images/svg/material-symbols_transportation.svg';
import Star from '../images/svg/star.svg';


export const ActivitiesLanging = () => {
    const [showMenu, setShowMenu] = useState(false);

    // Activities
    const [activities, setActivities] = useState([]);
    const [hasMore, setHasMore] = useState(true);
    const [activitiesLoading, setActivitiesLoading] = useState(false);

    // Tags search
    const [searchParams, _] = useSearchParams();
    const [activeMenuSearch, setActiveMenuSearch] = useState(searchParams.has('tag') ? searchParams.get('tag') : 'All Activities');
    useEffect(() => {
        if (searchParams.has("tag")) setActiveMenuSearch(searchParams.get("tag"));
    }, [searchParams]);

    // Property
    const [queries, setQueries] = useState({
        area: '',
        property: '',
        from: '',
        to: ''
    });
    const [properties, setProperties] = useState([]);

    const performSearch = useCallback(() => {
        const params = new URLSearchParams();
        params.append("limit", "4");
        params.append("fromPrice", queries.from);
        params.append("toPrice", queries.to);
        params.append("area", queries.area);

        axios.get(HOST_NAME + "api/property/search/", { params: params })
            .then(resp => {
                console.log(resp);
                if (resp.status === 200) {
                    setProperties(resp.data.data);
                }
            })
    }, [queries]);

    const handleShowMore = useCallback((clear = false) => {
        if (clear) setActivities([]);
        setActivitiesLoading(true);

        const params = new URLSearchParams();
        params.append("limit", "8");
        params.append("tag", activeMenuSearch);
        if (!clear && activities.length > 0) {
            params.append("start", activities[activities.length - 1].rating.normalized);
            params.append("productCode", activities[activities.length - 1].productCode);
        }

        axios.get(`${HOST_NAME}api/activities/`, { params: params })
            .then(resp => {
                if (resp.status === 200) {
                    if (clear)
                        setActivities(resp.data.data);
                    else
                        setActivities([...activities, ...resp.data.data]);
                    setHasMore(resp.data.more);
                }
            }).catch(err => console.log(err))
            .finally(() => setActivitiesLoading(false));
    }, [activities, setActivities, activeMenuSearch]);
    useEffect(() => handleShowMore(true), [activeMenuSearch]);

    const handleShowMenu = useCallback(() => {
        setShowMenu(!showMenu);
    }, [showMenu])

    const handleShowActiveMenu = useCallback((value) => {
        setActiveMenuSearch(value);
        setShowMenu(!showMenu);
        console.log("here", activeMenuSearch)
    }, [activeMenuSearch, showMenu])

    // Load initial data
    useEffect(performSearch, []);

    const menuItems = useMemo(() =>  ([
        {
            _id: 1,
            text: "All Activities"
        },
        {
            _id: 1,
            text: "Unique Experience",
            svg: Star,
        },
        {
            _id: 1,
            text: "Tickets & Passes",
            svg: EntypoTicket
        },
        {
            _id: 1,
            text: "Classes",
            svg: HealthiconsTraining
        },
        {
            _id: 1,
            text: "Speacial Occasions",
            svg: EmojioneMonotone
        },
        {
            _id: 1,
            text: "Outdoor Activities",
            svg: BaselineOutdoor
        },
        {
            _id: 1,
            text: "Transportation services",
            svg: SymbolsTransportation
        },
        {
            _id: 1,
            text: "Tours, Sightseeing",
            svg: BxsBinoculars
        },
        {
            _id: 1,
            text: "Art & Culture",
            svg: MapArtGallery
        },
        {
            _id: 1,
            text: "Food & Drinks",
            svg: BowlFood
        }
    ]),[])

    return (
        <div className="activities-langing-page-wrapper">
            <DefaultLayout coverImage={ActivitiesLandingPageCover} coverText="Activities in Mallorca">
                <div className="activities-langing-page">

                    <div className="explore-mallorca-activities-wrapper">
                        <Title text="Explore Mallorca Activities" />
                        <div className="explore-mallorca-activities">

                            <div className="drop-down-wrapper">
                                <button onClick={handleShowMenu}> {activeMenuSearch} <Isvg src={ArrowDown2} /></button>
                                {showMenu &&
                                    <div className="show-result">
                                        {menuItems.map((item, _idx) => {
                                            return (
                                                <div className={activeMenuSearch === item.text ? "active" : ""} key={_idx} onClick={() => handleShowActiveMenu(item.text)}> <Isvg src={item.svg} /> {item.text} </div>
                                            )
                                        })}
                                    </div>}
                            </div>

                            <div className="buttons">
                                {menuItems.map((item, _idx) => {
                                    return (
                                        <Link key={_idx} to={`/activities-landing?${createSearchParams({ tag: item.text }).toString()}`} style={{ textDecoration: "none" }}>
                                            <ButtonIcon key={_idx} icon={item.svg} text={item.text} active={activeMenuSearch === item.text} />
                                        </Link>
                                    )
                                })}

                            </div>
                        </div>
                    </div>

                    <div className="top-activities-wrapper">
                        <Title text="Top Activities in Mallorca" />
                        <div className="top-activities">
                            {activities.map((item, _idx) => {
                                return (
                                    <ThirdGuideCard key={item.id} image={item.thumbnail[0]} title={item.details.title[0]} id={item.slug}
                                        price={`${item.details.price_unit} ${item.details.price_amount}`} rating={item.rating.average} ratingCount={item.rating.total} />
                                )
                            })}
                        </div>
                        <button onClick={() => handleShowMore(false)} disabled={activitiesLoading || !hasMore}>{activitiesLoading ? "Loading..." : "Load more"}</button>
                    </div>

                    <div className="top-attractions-wrapper">
                        <Title text="Top 6 attractions in Mallorca" />
                        <div className="top-attractions">
                            {TOP_6_ATTRACTIONS.map((item, _idx) => {
                                return (
                                    <TopAttractions key={item._id} img={item.src} desc={item.desc} title={item.title} value={item._id} rating={item.rating} slug={item.slug} />
                                )
                            })}
                        </div>
                    </div>

                    <Subscribe />

                    <div className="property-offers-wrapper">
                        <Title text="Property Offers" />
                        <div className="property-offers">
                            <PropertyOffers values={queries} setValues={setQueries} handleSubmit={performSearch} />
                        </div>
                        <div className="wrapper-slider">
                            <Slider source={properties} type="props" secondGuideCardProp />
                        </div>
                    </div>

                    <div className="mallorca-gallery-images">
                        <Title text="Mallorca Gallery" />
                        <div className="gallery-images">
                            <ElfsightWidget widgetID="1b26b4ce-7ba2-4fb8-8e72-fdcdfa618769" />
                        </div>

                    </div>
                </div>
            </DefaultLayout>
        </div>
    )
}

export default ActivitiesLanging